.App {
  text-align: center;
  margin-top: 100px;
}

.nav-img{
  width: 50px;
}

.brand-name{
  margin-left: 40px;
  padding: 10px 10px;
  background-color: green;
  color: #fff;
  border-radius: 10px;
}

.custom:hover{
  background-color: green;
  border-radius: 10px;
  color: #fff;
}

.footer {
  margin: 30px 50px;
}

.footer a{
  text-decoration: none;
  margin: 0px 10px;
}

.footer a img{
  width: 30px;
}

.footer p{margin-top: 10px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
