.Name{
    width: 92%;
    padding: 20px 100px;
    background-color: royalblue;
    color: #fff;
    border-radius: 10px;
    margin-top: 30px;
    padding-left: 350px;
    position: absolute;
    font-family: 'Lobster', cursive;
}

.about-me{
    width: 280px;
    height: 100%;
    background-color: rgb(86, 95, 124);
    padding: 10px 10px;
    margin-left: 30px;
    border-radius: 10px;
    position: absolute;
    color: #fff;
    text-align: center;
}

.my-img{
    width: 80%;
    margin-top: 20px;
    border-radius: 10px;
    border: 1px solid lightgray;
}

.contact{
    margin-top: 30px;
}

.contact hr{
    background-color: lightgray;
}

.contact-info{
    text-align: left;
}

.contact-info img{
    width: 30px;
    display: inline-block;
    margin-right: 10px;
}

.contact-info p{
    display: inline-block;
    width: 200px;
    color: lightgray;
}

.one img{
    margin-top: -30px;
}

.login-form{
    position: absolute;
    margin-top: 200px;
    border: 1px solid slategrey;
    text-align: left;
    padding: 20px 20px;
    border-radius: 5px;
    background-color: #565F7C;
    margin-left: 400px;
    color: #fff;
}

.login-form input:focus{
    background-color:#4169E1;
    color: #fff;
}

.login-form button{
    width: 100%;
    border-radius: 5px;
    background-color: #4169E1;
    color: #fff;
}

.login-form button:hover{
    width: 100%;
    border-radius: 5px;
    background-color: inherit;
    border: 1px solid #4169E1;
    color: #fff;
}

.hobbies{
    margin-top: 5px;
}

.hobbies img{
    width: 50px;
    border: 1px solid #fff;
    border-radius: 50px;
    margin: 0px 5px;
}

.hobbies img:hover{
    opacity: .5;
}